<template>
  <div id="wangeEdit">
    <div id="wangeEditToolBar" class="toolbar"></div>
    <div id="wangeEditWrapper" v-on:mouseout="putEditContent" style=""></div>
  </div>
</template>
<script>
import E from "wangeditor";

export default {
    model: {
        prop: 'content',
        event: 'change'
    },
    props:{
        content:{
            type:String
        }
    },
    name:'wangeEdit',
    data(){
        return{
            newContent:this.content,
            editor:Object
        }
    },
    watch:{
        content:function(val, oldVal) {
            this.newContent = val;
            //document.getElementById('HpEdit_editContent').innerHTML = val;
            this.setEditHTMLContent(val);
        }
    },
    mounted(){
        this.createEdit();
    },
    methods:{
        createEdit(){
            this.editor = new E('#wangeEditToolBar','#wangeEditWrapper');
            this.editor.customConfig.menus = [
                'head',  // 
                'bold',  // 
                'fontSize',  // 
                'fontName',  // 
                'italic',  // 
                'underline',  // 
                'strikeThrough',  // 
                'foreColor',  // 
                'backColor',  // 
                'link',  // 
                'list',  // 
                'justify',  // 
                'quote',  // 
                'emoticon',  // 
                'image',  // 
                'table',  // 
                'video',  // 
                'code',  // 
                'undo',  // 
                'redo'  // 
            ];
            this.editor.customConfig.uploadImgShowBase64 = true   //  base64 
            this.editor.customConfig.pasteFilterStyle = false;
            this.editor.customConfig.uploadImgServer = '/uploadImage'  // 
            this.editor.customConfig.linkImgCallback = function (url) {
                console.log(url) // url 
            }
            this.editor.customConfig.uploadImgHooks = {
                before: function (xhr, editor, files) {
                    // 
                    // xhr  XMLHttpRequst editor files 
                    //xhr.open('POST', 'http://localhost:3000/uploadImage', true);
                    //console.log(files,'')
                    //  {prevent: true, msg: 'xxxx'} 
                    // return {
                    //     prevent: true,
                    //     msg: ''
                    // }
                },
                success: function (xhr, editor, result) {
                    // 
                    // xhr  XMLHttpRequst editor result 
                    xhr.open('GET', result.data[0], true);
                    //alert('');
                },
                fail: function (xhr, editor, result) {
                    // 
                    // xhr  XMLHttpRequst editor result 
                    alert('');
                },
                error: function (xhr, editor) {
                    // 
                    // xhr  XMLHttpRequst editor 
                    alert('');
                },
                timeout: function (xhr, editor) {
                    // 
                    // xhr  XMLHttpRequst editor 
                    alert('');
                },

                //  {errno:0, data: [...]} 
                //  JSON 
                /*customInsert: function (insertImg, result, editor) {
                    // 
                    // insertImg editor result 

                    //  {url:'....'} 
                    //let url = result.data[0].substring(result.data[0].indexOf('/'));
                    let url = result.data[0];
                   // console.log(url);
                    insertImg(url);
                    // result  JSON 
                }*/
    
            };
            this.editor.create();
        },
        getEditHTMLContent(){
            return this.editor.txt.html()
        },
        getEditTXTContent(){
            return this.editor.txt.text()
        },
        setEditHTMLContent(val){
            this.editor.txt.html(val)
        },
        /**
         * 
         */
        putEditContent(){
            let articleContent     = this.getEditHTMLContent();
            let articleContentText = this.getEditTXTContent();
            this.$emit('change', articleContent);                    // v-modle input
            this.$emit('getArticle',{articleContent,articleContentText});
        }
    }
}
</script>
<style  lang='less'>
/** */

#wangeEdit {
  position: relative;
  background: white;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 545px;
  .toolbar {
    border-bottom: 1px solid rgba(146, 146, 146, 0.8);
  }
  #wangeEditWrapper {
    height: 94%;
    border-bottom: 1px solid rgba(146, 146, 146, 0.8);
  }
}
</style>